
'use client'

import { Button } from '@/components/ui/button'
import { Sheet, SheetContent, SheetTrigger } from '@/components/ui/sheet'
import { Menu, Zap, Users, Bot, ArrowRight, Building2, Cloud, Monitor, Search, Sparkles, Database, MessageSquare, BarChart3, Settings, Shield } from 'lucide-react'
import Image from 'next/image'
import Link from 'next/link'

export default function Home() {
  const features = [
    { 
      name: 'AI-Powered Widgets', 
      description: 'Create intelligent dashboards with natural language commands',
      icon: Sparkles,
      href: '/dashboard'
    },
    { 
      name: 'ServiceNow Integration', 
      description: 'Connect seamlessly with your ServiceNow instance for real-time data',
      icon: Database,
      href: '/dashboard'
    },
    { 
      name: 'Smart Chat Interface', 
      description: 'Contextual AI assistance within each widget for deeper insights',
      icon: MessageSquare,
      href: '/dashboard'
    },
    { 
      name: 'Visual Analytics', 
      description: 'Interactive charts and dashboards for incident management',
      icon: BarChart3,
      href: '/dashboard'
    },
    { 
      name: 'Workspace Management', 
      description: 'Save and organize multiple workspace configurations',
      icon: Settings,
      href: '/dashboard'
    },
    { 
      name: 'Enterprise Security', 
      description: 'Built with security-first architecture for enterprise environments',
      icon: Shield,
      href: '/dashboard'
    },
  ]

  const useCases = [
    { name: 'Incident Management', description: 'Track and resolve incidents efficiently' },
    { name: 'Change Management', description: 'Monitor and approve change requests' },
    { name: 'Problem Analysis', description: 'Identify root causes and prevent recurrence' },
    { name: 'Performance Monitoring', description: 'Real-time insights into system performance' },
  ]

  const clientLogos = [
    'ServiceNow', 'Microsoft', 'Google', 'Amazon'
  ]

  const clientIcons = {
    'ServiceNow': Database,
    'Microsoft': Monitor,
    'Google': Search,
    'Amazon': Cloud
  }

  return (
    <div className="min-h-screen bg-white text-gray-900">
      {/* Navigation */}
      <nav className="fixed top-0 w-full bg-white/95 backdrop-blur-sm border-b border-gray-200 z-50">
        <div className="container mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <Link href="/" className="flex items-center hover:scale-105 transition-transform duration-200">
              <Image 
                src="/bablephish_logo.svg" 
                alt="BabelPhish" 
                width={32} 
                height={32} 
                className="mr-2" 
              />
              <span className="text-xl font-bold text-primary">BabelPhish</span>
            </Link>

            {/* Desktop Navigation */}
            <div className="hidden md:flex items-center space-x-8">
              <Link href="/dashboard" className="hover:text-primary transition-colors">Dashboard</Link>
              <Link href="/#features" className="hover:text-primary transition-colors">Features</Link>
              <Link href="/#about" className="hover:text-primary transition-colors">About</Link>
              <Link href="/contact" className="hover:text-primary transition-colors">Contact</Link>
            </div>

            {/* CTA Button */}
            <div className="hidden md:block">
              <Link href="/dashboard">
                <Button className="bg-primary hover:bg-primary/90 text-white transform hover:scale-105 transition-all duration-200">
                  Get Started
                </Button>
              </Link>
            </div>

            {/* Mobile Menu */}
            <Sheet>
              <SheetTrigger className="md:hidden">
                <Menu className="h-6 w-6" />
              </SheetTrigger>
              <SheetContent side="right" className="bg-white border-gray-200">
                <div className="space-y-4 mt-8">
                  <Link href="/dashboard" className="block py-2">Dashboard</Link>
                  <Link href="/#features" className="block py-2">Features</Link>
                  <Link href="/#about" className="block py-2">About</Link>
                  <Link href="/contact" className="block py-2">Contact</Link>
                  <Link href="/dashboard">
                    <Button className="w-full bg-primary hover:bg-primary/90 text-white">
                      Get Started
                    </Button>
                  </Link>
                </div>
              </SheetContent>
            </Sheet>
          </div>
        </div>
      </nav>

      {/* Hero Section */}
      <section className="pt-24 pb-16 px-4 bg-gradient-to-br from-blue-50 to-white">
        <div className="container mx-auto">
          <div className="grid lg:grid-cols-2 gap-12 items-center">
            <div className="space-y-8">
              <h1 className="text-5xl lg:text-6xl font-bold mb-6">
                <span className="text-foreground">AI-Powered</span><br />
                <span className="text-primary">ServiceNow</span><br />
                <span className="text-foreground">Experience</span>
              </h1>
              <p className="text-xl lg:text-2xl text-muted-foreground mb-8">
                Transform your ServiceNow workflows with intelligent widgets, natural language processing, and contextual AI assistance.
              </p>
              <div className="flex flex-col sm:flex-row gap-4">
                <Link href="/dashboard">
                  <Button size="lg" className="bg-primary hover:bg-primary/90 text-white transform hover:scale-105 transition-all duration-200 shadow-lg">
                    Try Dashboard
                  </Button>
                </Link>
                <Link href="/#features">
                  <Button size="lg" variant="outline" className="border-primary text-primary hover:bg-primary hover:text-white transition-all duration-200">
                    Learn More
                  </Button>
                </Link>
              </div>
            </div>
            <div className="flex justify-center">
              <div className="relative">
                <div className="absolute inset-0 bg-primary/20 rounded-full blur-3xl opacity-30"></div>
                <div className="relative z-10 animate-float">
                  <Image 
                    src="/bablephish_logo.svg" 
                    alt="BabelPhish Logo" 
                    width={256} 
                    height={256} 
                    className="drop-shadow-2xl"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Trusted By Section */}
      <section className="py-16 px-4 bg-gray-50">
        <div className="container mx-auto">
          <div className="text-center mb-12">
            <h3 className="text-2xl font-semibold mb-4">Trusted by Enterprise Teams</h3>
          </div>
          <div className="flex justify-center">
            <div className="grid grid-cols-2 md:grid-cols-4 gap-12 max-w-2xl">
              {clientLogos.map((logo, index) => {
                const IconComponent = clientIcons[logo];
                return (
                  <div 
                    key={logo} 
                    className="flex flex-col items-center justify-center space-y-4 opacity-60 hover:opacity-100 transition-all duration-300 hover:scale-110"
                  >
                    <div className="w-16 h-16 bg-white rounded-full flex items-center justify-center shadow-sm">
                      <IconComponent className="h-8 w-8 text-primary" />
                    </div>
                    <div className="text-sm font-medium text-gray-600 hover:text-primary transition-colors text-center">
                      {logo}
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section id="features" className="py-16 px-4">
        <div className="container mx-auto">
          <div className="text-center mb-12">
            <h2 className="text-4xl font-bold mb-4">Powerful Features</h2>
            <p className="text-xl text-muted-foreground">Everything you need to streamline your ServiceNow experience</p>
          </div>
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
            {features.map((feature, index) => {
              const IconComponent = feature.icon;
              return (
                <div 
                  key={feature.name} 
                  className="bg-white p-6 rounded-lg border border-gray-200 hover:border-primary/50 transition-all duration-300 hover:shadow-lg hover:shadow-primary/10 hover:-translate-y-1 group"
                >
                  <IconComponent className="h-12 w-12 text-primary mb-4 group-hover:scale-110 transition-transform duration-200" />
                  <h3 className="text-xl font-semibold mb-2 group-hover:text-primary transition-colors">{feature.name}</h3>
                  <p className="text-muted-foreground mb-4">{feature.description}</p>
                  <Link href={feature.href} className="text-primary hover:text-primary/80 font-medium inline-flex items-center">
                    Try it now <ArrowRight className="ml-2 h-4 w-4" />
                  </Link>
                </div>
              );
            })}
          </div>
        </div>
      </section>

      {/* Use Cases Section */}
      <section className="py-16 px-4 bg-gray-50">
        <div className="container mx-auto">
          <div className="text-center mb-12">
            <h2 className="text-4xl font-bold mb-4">Perfect for Your Workflow</h2>
            <p className="text-xl text-muted-foreground">Streamline operations across all ServiceNow modules</p>
          </div>
          <div className="grid md:grid-cols-2 gap-8">
            {useCases.map((useCase, index) => (
              <div 
                key={useCase.name} 
                className="bg-white p-6 rounded-lg border border-gray-200 hover:border-primary/50 transition-all duration-300 hover:shadow-lg hover:shadow-primary/10 hover:-translate-y-1"
              >
                <BarChart3 className="h-12 w-12 text-primary mb-4" />
                <h3 className="text-xl font-semibold mb-2">{useCase.name}</h3>
                <p className="text-muted-foreground mb-4">{useCase.description}</p>
                <Link href="/dashboard" className="text-primary hover:text-primary/80 font-medium inline-flex items-center">
                  Try it now <ArrowRight className="ml-2 h-4 w-4" />
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-16 px-4">
        <div className="container mx-auto">
          <div className="bg-gradient-to-r from-primary to-primary/80 rounded-2xl p-12 text-center text-white">
            <h2 className="text-4xl font-bold mb-4">Ready to Transform Your ServiceNow Experience?</h2>
            <p className="text-xl mb-8 text-primary-foreground/80">Start using intelligent widgets and AI-powered insights today</p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Link href="/dashboard">
                <Button size="lg" variant="secondary" className="bg-white text-primary hover:bg-gray-100 transform hover:scale-105 transition-all duration-200">
                  Start Free Trial
                </Button>
              </Link>
              <Link href="/contact">
                <Button size="lg" variant="outline" className="border-white text-white hover:bg-white hover:text-primary transition-all duration-200">
                  Contact Sales
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-12 px-4">
        <div className="container mx-auto">
          <div className="grid md:grid-cols-4 gap-8">
            <div>
              <div className="flex items-center mb-4">
                <Image 
                  src="/bablephish_logo.svg" 
                  alt="BabelPhish" 
                  width={32} 
                  height={32} 
                  className="mr-2" 
                />
                <span className="text-xl font-bold">BabelPhish</span>
              </div>
              <p className="text-gray-400">AI-powered ServiceNow experience for modern enterprises.</p>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Product</h4>
              <ul className="space-y-2">
                <li>
                  <Link href="/dashboard" className="text-gray-400 hover:text-white transition-colors">Dashboard</Link>
                </li>
                <li>
                  <Link href="/#features" className="text-gray-400 hover:text-white transition-colors">Features</Link>
                </li>
                <li>
                  <Link href="/integrations" className="text-gray-400 hover:text-white transition-colors">Integrations</Link>
                </li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Company</h4>
              <ul className="space-y-2">
                <li>
                  <Link href="/#about" className="text-gray-400 hover:text-white transition-colors">About</Link>
                </li>
                <li>
                  <Link href="/contact" className="text-gray-400 hover:text-white transition-colors">Contact</Link>
                </li>
                <li>
                  <Link href="/support" className="text-gray-400 hover:text-white transition-colors">Support</Link>
                </li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Connect</h4>
              <ul className="space-y-2">
                <li>
                  <a href="https://github.com/babelphish" className="text-gray-400 hover:text-white transition-colors">GitHub</a>
                </li>
                <li>
                  <a href="https://linkedin.com/company/babelphish" className="text-gray-400 hover:text-white transition-colors">LinkedIn</a>
                </li>
                <li>
                  <a href="mailto:hello@babelphish.ai" className="text-gray-400 hover:text-white transition-colors">Email</a>
                </li>
              </ul>
            </div>
          </div>
          <div className="border-t border-gray-800 mt-8 pt-8 text-center text-gray-400">
            <p>&copy; 2024 BabelPhish. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  )
}
